import styled from '@emotion/styled'
import { COLOR } from '../utils/theme'
import { Link } from 'gatsby'

export const DarkLink = styled(Link)({
  color: COLOR.dark,
  ':hover': {
    color: COLOR.dark,
    textDecoration: 'underline'
  }
})

export const DarkA = styled.a({
  color: COLOR.dark,
  ':hover': {
    color: COLOR.dark,
    textDecoration: 'underline'
  }
})

export default DarkLink
